import React from 'react';
import TourCard from "../../Shared/TourCard";
import { Button, Col } from 'reactstrap';
import useFetch from '../../hooks/useFetch';
import { NavLink } from 'react-router-dom';

// Assuming BASE_URL is defined somewhere in your project
const BASE_URL = process.env.REACT_APP_BASE_URL || 'http://localhost:4000'; 

const FeaturedToursList = () => {
  // Fetch the featured tours using your custom hook
  const { data: featuredTours, loading } = useFetch(`${BASE_URL}/tours/search/getFeaturedTours`);

  // Loading state to display the loader
  if (loading) {
    return (
      <div className="loader-container">
        <div className="loader" />
        <div className="loading-text">Loading...</div>
      </div>
    );
  }

  // Main content rendering after loading
  return (
    <>
      {Array.isArray(featuredTours) &&
        featuredTours.map((tour) => (
          <Col lg="3" md="6" sm="6" className="mb-4" key={tour._id}>
            <TourCard tour={tour} />
          </Col>
        ))}
        
      <div className="viall__btn">
        <NavLink to="/tours">
          <Button className='btn primary__btn'>View All Tours</Button>
        </NavLink>
      </div>
    </>
  );
};

export default FeaturedToursList;
